<template>
  <article class="designTouNeeds_container">
    <h3 class="wow fadeInUp">{{ title }}</h3>
    <section class="content">
      <div class="description wow fadeInUp">
        <img
          :src="getImgUrl(designYourNeeds.img)"
          :alt="title"
          loading="lazy"
          draggable="false"
        />
        <p>{{ designYourNeeds.description }}</p>
        <button @click="showModal">
          {{ designYourNeeds.buttonText }}<i class="fas fa-paint-brush-alt" />
        </button>
      </div>
      <div class="image_container wow bounceIn">
        <img
          src="@/assets/designYourNeeds.webp"
          alt="design your needs Logo"
          loading="lazy"
          draggable="false"
        />
      </div>
    </section>
    <ModalComponent v-model="isShow" :close="closeModal">
      <section class="modal_container">
        <!-- Frist Page -->
        <article
          class="choose_props show"
          ref="chooseProps"
          @animationend="animationEndHandler"
        >
          <div
            class="prop-category-box"
            v-for="(category, i) in designYourNeeds.category"
            :key="i"
          >
            <h4 class="prop-category">{{ category }}</h4>
            <div class="props_list">
              <div
                v-for="(prop, index) in designYourNeeds.data"
                :key="index"
                :class="{
                  disable: prop.fixed,
                  hidden: prop.category !== category,
                }"
              >
                <ProjectProperty
                  v-if="prop.category === category"
                  @click="toggleProperty($event, prop)"
                  @keyup="toggleProperty($event, prop)"
                  :ref="prop.name"
                  :property="prop"
                />
              </div>
            </div>
          </div>

          <div class="footer">
            <p>
              <bdi>
                {{ designYourNeeds.designYourNeedsMessage + " " }}
              </bdi>
              <span>{{ totalPrice + " " + currency }}</span>
            </p>
            <div class="btns_container">
              <button @click="closeModal" class="cancleButton">
                {{ sendRequestData.cancleButtonText }}
              </button>
              <button @click="nextAction" class="nextButton">
                {{ sendRequestData.nextButtonText }}
                <i class="fas fa-forward" />
              </button>
            </div>
          </div>
        </article>
        <!-- Second Page -->
        <article
          ref="submitContainer"
          class="submit_Container"
          @animationend="animationEndHandler"
        >
          <SendClientDataToBuy
            :backAction="backAction"
            :currency="currency"
            :designYourNeeds="designYourNeeds"
            :totalPrice="totalPrice"
            :selectedProps="selectedProps"
            :programName="programName"
            :closeModal="closeModal"
          />
        </article>
      </section>
    </ModalComponent>
  </article>
</template>

<script>
import { getImgUrl } from "@/util/methods";

import SendClientDataToBuy from "@/components/SendClientDataToBuy";
import ProjectProperty from "@/components/ProjectProperty";
export default {
  name: "ProjectPageDesignNeeds",
  data() {
    return {
      isShow: false,
      totalPrice: 0,
      selectedProps: {},
    };
  },
  components: {
    SendClientDataToBuy,
    ProjectProperty,
  },
  methods: {
    getImgUrl,
    showModal() {
      this.isShow = true;
      this.totalPrice = this.designYourNeeds.basePrice;
      this.selectedProps = this.defaultOption();
      document.body.style.overflow = "hidden";
    },
    closeModal() {
      this.isShow = false;
      document.body.style.overflow = "auto";
    },
    toggleProperty(event, prop) {
      if (!prop.fixed && event.target.nodeName != "INPUT") {
        if (this.selectedProps[prop.name]) {
          /* Check existWith Props */
          const existWith = prop.existWith?.filter(
            (propname) => this.selectedProps[propname]
          );
          if (existWith && existWith.length) {
            return;
          }
          // Sub From TotalPrice
          this.totalPrice -= this.selectedProps[prop.name].totalPrice;
          delete this.selectedProps[prop.name];
          this.$refs[prop.name].toggleProperty();
        } else {
          const price = this.$refs[prop.name].getPrice();
          const num = this.$refs[prop.name].getNum();
          // Add To TotalPrice
          this.totalPrice += price;
          prop.totalPrice = price;
          prop.num = num;
          this.selectedProps[prop.name] = prop;
          this.$refs[prop.name].toggleProperty();
          /* Check Oppiste Props To Clear Selection */
          const { opposite } = prop;
          if (opposite) {
            for (const oppositeProp of opposite) {
              // Sub From TotalPrice
              if (this.selectedProps[oppositeProp]) {
                this.totalPrice -= this.selectedProps[oppositeProp].totalPrice;
                this.$refs[oppositeProp].toggleProperty();
                delete this.selectedProps[oppositeProp];
              }
            }
          }
          /* Check Depends Props To Add Selection */
          const { dependsOn } = prop;
          if (dependsOn) {
            for (const dependsPropName of dependsOn) {
              // Add Selection
              if (!this.selectedProps[dependsPropName]) {
                const propery = this.designYourNeeds.data.filter(
                  (prop) => prop.name === dependsPropName
                )[0];
                const dependsPropPrice = this.$refs[dependsPropName].getPrice();
                const dependsPropNum = this.$refs[dependsPropName].getNum();
                this.totalPrice += dependsPropPrice;
                propery.totalPrice = dependsPropPrice;
                propery.num = dependsPropNum;
                this.selectedProps[dependsPropName] = propery;
                this.$refs[dependsPropName].toggleProperty();
              }
            }
          }
        }
      } else if (
        event.target.nodeName == "INPUT" &&
        this.selectedProps[prop.name]
      ) {
        const newPrice = this.$refs[prop.name].getPrice();
        const newNum = this.$refs[prop.name].getNum();
        const oldData = this.selectedProps[prop.name];
        this.totalPrice -= oldData.totalPrice;
        this.totalPrice += newPrice;
        prop.num = newNum;
        oldData.totalPrice = newPrice;
      }
    },

    nextAction() {
      this.$refs["chooseProps"].classList.remove("show");
      this.$refs["chooseProps"].classList.add("hide");
      this.$refs["submitContainer"].classList.remove("hide");
      this.$refs["submitContainer"].classList.add("show");
    },
    backAction() {
      this.$refs["submitContainer"].classList.remove("show");
      this.$refs["submitContainer"].classList.add("hide");

      this.$refs["chooseProps"].classList.remove("hide");
      this.$refs["chooseProps"].classList.add("show");
    },
    animationEndHandler(e) {
      if (e.target.className.indexOf("hide") > -1) {
        e.target.classList.remove("hide");
      }
    },
    defaultOption() {
      return this.designYourNeeds.data
        .filter((item) => item.fixed)
        .reduce((a, b) => {
          a[b.name] = b;
          return a;
        }, {});
    },
  },
  computed: {
    currency() {
      const { lang } = this.$store.state;

      return this.$store.state[lang].currency;
    },
    sendRequestData() {
      const { lang } = this.$store.state;
      return this.$store.state[lang].sendRequestData;
    },
  },
  props: ["title", "designYourNeeds", "programName"],
};
</script>
